body {
  margin: 0 auto;
  overflow-x: hidden;
}

.container {
  margin: 0 165px;
}
@media screen and (max-width: 1385px) {
  .container {
    margin: 0 100px;
  }
}
@media screen and (max-width: 1115px) {
  .container {
    margin: 0 50px;
  }
}

.image__block-first {
  margin-top: 0;
  margin-bottom: 120px;
  width: 100%;
  height: 927px;
  background-image: url("../img/imgonline-com-ua-Resize-DlRXsc4WVsD3h.jpg");
  background-repeat: no-repeat;
  background-size: cover;
}
.image__block-first .best-place {
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  flex-direction: row;
}
.image__block-first .best-place__circle {
  border-radius: 50%;
  border: 2px solid white;
  width: 80px;
  height: 80px;
  margin-top: 165px;
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  cursor: pointer;
  overflow: hidden;
  position: relative;
  background: none;
  transition: all ease 0.5s;
}
.image__block-first .best-place__circle::before {
  transition: all 0.5s ease;
  content: "";
  width: 0%;
  height: 100%;
  background: #B85C38;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
}
.image__block-first .best-place__circle__img {
  fill: white;
}
.image__block-first .best-place__circle:hover::before {
  width: 100%;
}
.image__block-first .best-place__circle:hover {
  transform: scale(0.9);
  background-color: #FF804E;
}
.image__block-first .main__title {
  color: white;
  margin-top: 40px;
  font-family: "Nunito";
  text-align: center;
  font-size: 72px;
  font-weight: 700;
}
.image__block-first .main__description {
  color: white;
  margin-top: 8px;
  font-family: "Nunito";
  text-align: center;
  font-size: 24px;
}
.image__block-first .main__button_block {
  margin-bottom: 400px;
  margin-top: 58px;
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  flex-direction: row;
}
.image__block-first .main__button_block_demo {
  font-family: "Nunito";
  font-size: 20px;
  padding: 18px 32px;
  background-color: #B85C38;
  border-radius: 40px;
  color: white;
  transition: all ease 0.5s;
}
.image__block-first .main__button_block_demo:hover {
  transform: scale(0.9);
  background-color: #FF804E;
}
.image__block-first .main__button_block_buy {
  margin-left: 24px;
  font-family: "Nunito";
  font-size: 20px;
  padding: 18px 32px;
  border: 2px solid #FFFFFF;
  border-radius: 40px;
  background: none;
  position: relative;
  overflow: hidden;
  color: white;
  transition: all ease 0.5s;
}
.image__block-first .main__button_block_buy::before {
  transition: all 0.5s ease;
  content: "";
  width: 0%;
  height: 100%;
  background: #B85C38;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
}
.image__block-first .main__button_block_buy-text {
  color: white;
}
.image__block-first .main__button_block_buy:hover::before {
  width: 100%;
}
.image__block-first .main__button_block_buy:hover {
  transform: scale(0.9);
  background-color: #FF804E;
}

.header {
  display: flex;
  padding-top: 89px;
}
.header__nav {
  margin: 0 auto;
}
.header__nav_container {
  display: flex;
  flex-wrap: wrap;
}
.header__nav_link {
  font-family: "Nunito";
  font-size: 18px;
  display: flex;
  align-items: center;
  text-decoration: none;
  list-style: none;
  transition: all ease 0.5s;
}
.header__nav_link a {
  color: white;
}
.header__nav_link:first-child {
  margin-right: 176px;
  font-size: 24px;
  font-weight: 700;
}
.header__nav_link:nth-child(2) {
  margin-right: 50px;
}
.header__nav_link:nth-child(3) {
  margin-right: 50px;
}
.header__nav_link:nth-child(5) {
  margin-left: 50px;
}
.header__nav_link:last-child {
  margin-left: 176px;
  padding: 12px 32px;
  background-color: #B85C38;
  border-radius: 40px;
  cursor: pointer;
}
.header__nav_link:last-child a {
  color: white;
}
.header__nav_link:last-child:hover {
  background-color: #FF804E;
}
.header__nav_link:hover {
  transform: scale(0.9);
}
@media screen and (max-width: 1385px) {
  .header__nav_link:first-child {
    margin-right: 100px;
  }
  .header__nav_link:last-child {
    margin-left: 100px;
  }
}
@media screen and (max-width: 1115px) {
  .header__nav_link {
    font-size: 15px;
  }
  .header__nav_link:first-child {
    margin-right: 100px;
    font-size: 24px;
    font-weight: 700;
  }
}
.main {
  margin: 0 auto;
}
.main .features {
  margin: 0 auto;
  max-width: 1110px;
}
.main .features__title {
  text-align: center;
  font-size: 64px;
  font-family: "Nunito";
  font-weight: 700;
}
.main .features__description {
  color: #BDBDBD;
  text-align: center;
  font-size: 24px;
  font-family: "Nunito";
  font-weight: 400;
  margin-top: 24px;
}
.main .features__container {
  margin-top: 132px;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
.main .features__container__card {
  width: 350px;
  margin: 79px auto 0 auto;
  text-align: center;
}
.main .features__container__card img {
  width: 48px;
}
.main .features__container__card__name {
  color: #B85C38;
  font-size: 24px;
  font-family: "Nunito";
  font-weight: 700;
  margin-top: 24px;
}
.main .features__container__card__description {
  margin-top: 18px;
  font-size: 14px;
  color: #BDBDBD;
  font-family: "Nunito";
  font-weight: 400;
}
.main .time {
  display: flex;
  justify-content: center;
  width: 100%;
  height: 600px;
  margin-top: 153px;
}
.main .time__wrapper {
  align-items: center;
  display: flex;
  justify-content: space-between;
  width: 100%;
  background-color: #B85C38;
}
.main .time__content {
  width: 70%;
  align-items: flex-start;
  display: flex;
  flex-direction: column;
  justify-content: start;
  margin-left: 90px;
}
.main .time__title {
  font-size: 48px;
  font-family: "Nunito";
  font-weight: 700;
}
.main .time__description {
  margin-top: 18px;
  font-size: 24px;
  font-family: "Nunito";
}
.main .time ul {
  margin-top: 24px;
  display: flex;
  align-items: center;
  justify-content: left;
}
.main .time ul li:last-child {
  margin-left: 24px;
  font-size: 18px;
  font-family: "Nunito";
}
@media screen and (max-width: 1280px) {
  .main .time__wrapper {
    height: 458.59px;
  }
  .main .time__content {
    margin-right: 165px;
  }
  .main .time__title {
    font-size: 40px;
  }
  .main .time__description {
    font-size: 20px;
  }
}
@media screen and (max-width: 1130px) {
  .main .time__title {
    font-size: 35px;
  }
  .main .time__description {
    font-size: 16px;
  }
  .main .time__content {
    margin-right: 100px;
  }
  .main .time__wrapper ul li img {
    width: 48px;
  }
  .main .time__wrapper ul li:last-child {
    font-size: 14px;
  }
}
@media screen and (max-width: 1000px) {
  .main .time__wrapper {
    height: 305.72px;
  }
  .main .time__title {
    font-size: 25px;
  }
  .main .time__description {
    font-size: 13px;
  }
  .main .time__content {
    margin-right: 50px;
    width: 50%;
  }
  .main .time__wrapper ul {
    margin-top: 15px;
  }
  .main .time__wrapper ul li img {
    width: 36px;
  }
  .main .time__wrapper ul li:last-child {
    font-size: 14px;
  }
}
@media screen and (max-width: 1280px) {
  .main .time-img {
    width: 600px;
  }
}
@media screen and (max-width: 1130px) {
  .main .time-img {
    width: 600px;
  }
}
@media screen and (max-width: 1000px) {
  .main .time-img {
    width: 400px;
  }
}